$font-primary: 'PT Sans', Arial, serif;

// Overrides
$grid-gutter-width: 40px !default;
$border-radius-base:  4px !default;
$padding-base-vertical: 14px !default;

$brand-primary: #FA5555 !default;
$brand-secondary: #00ADB5 !default;
$brand-tertiary: #FFC12D !default;



$brand-white: #fff;
$brand-black: #000;
$brand-darker: #333;
$brand-gray: #ccc;
$brand-lighter: #e9e9e9;
$brand-body-color: #f2f2f2;
$brand-selection-color: #f9f6f0;
$brand-overlay-color: #3b3d40;
$brand-bg-footer: #41444B;
$brand-bg-color: $brand-white;

$input-border-focus:  $brand-primary !default;
$form-group-margin-bottom:       30px !default;



// Mixin
@mixin transition($transition) {
    -moz-transition:    $transition ease;
    -o-transition:      $transition ease;
    -webkit-transition: $transition ease;
    -ms-transition: 		$transition ease;
    transition:         $transition ease;
}
@mixin inline-block() {
    display:-moz-inline-stack;
    display:inline-block;
    zoom:1;
    *display:inline;
}
@mixin flex() {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
}
@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    border-radius: $radius;
}
@mixin flex() {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}
@mixin flexwrap() {
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -moz-flex-wrap: wrap;
}

@mixin fh5co-gradient() {
    // Gradient
    background-color: $brand-primary;
    /* IE9, iOS 3.2+ */
    background-image: url(data:image/svg+xml;base64,);
    background-image: -webkit-gradient(linear, 0% 0%, 100% 100%,color-stop(0, rgb(82, 211, 170)),color-stop(1, rgb(63, 149, 234)));
    /* Android 2.3 */
    background-image: -webkit-repeating-linear-gradient(top left,rgb(82, 211, 170) 0%,rgb(63, 149, 234) 100%);
    /* IE10+ */
    background-image: repeating-linear-gradient(to bottom right,rgb(82, 211, 170) 0%,rgb(63, 149, 234) 100%);
    background-image: -ms-repeating-linear-gradient(top left,rgb(82, 211, 170) 0%,rgb(63, 149, 234) 100%);
}

@font-face {
    font-family: 'icomoon';
    src:url('../fonts/icomoon/icomoon.eot?srf3rx');
    src:url('../fonts/icomoon/icomoon.eot?srf3rx#iefix') format('embedded-opentype'),
    url('../fonts/icomoon/icomoon.ttf?srf3rx') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?srf3rx') format('woff'),
    url('../fonts/icomoon/icomoon.svg?srf3rx#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

@mixin icomoon() {

    font-family: 'icomoon';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

}

