// Template Base
body {
    font-family: $font-primary;
    font-weight: 300;
    font-size: 20px;
    line-height: 1.5;
    color: lighten($brand-darker, 20%);
    color: #7f7f7f;
    background: $brand-bg-color;
    height: 100%;
    position: relative;

}
a {
    color: $brand-primary;
    @include transition(.5s);
    &:hover, &:active, &:focus {
        color: $brand-primary;
        outline: none;
    }
}
p {
    margin-bottom: 1.5em;
    font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
    color: $brand-darker;
    font-family: $font-primary;
    font-weight: 400;
    margin: 0 0 30px 0;
}
::-webkit-selection {
    color: $brand-white;
    background: $brand-secondary;
}

::-moz-selection {
    color: $brand-white;
    background: $brand-secondary;
}

::selection {
    color: $brand-white;
    background: $brand-secondary;
}


#fh5co-header {
    // position: relative;
    position: absolute;
    z-index: 99;
    width: 100%;
    opacity: 1;
    top: 0;

    .navbar {
        padding-bottom: 0;
        margin-bottom: 0;
    }
    #navbar {

        .navbar-right {
            margin-right: 0;
            text-align: center;
            @media screen and (max-width: $screen-md) {
                padding-bottom: 20px;
            }
        }
        li {

            a {
                font-family: $font-primary;
                color: rgba(0,0,0,.5);
                position: relative;
                font-size: 16px;
                font-weight: 400;
                @media screen and (max-width :$screen-md) {
                    padding-left: 10px!important;
                    padding-right: 10px!important;
                    font-size: 20px;
                }

                span {
                    position: relative;
                    display: block;
                    padding-bottom: 2px;
                }
                &:hover {
                    // color: $brand-primary;
                    color: $brand-black;
                    // color: rgba(255,255,255,1);
                    span {
                        &:before {
                            visibility: visible;
                            -webkit-transform: scaleX(1);
                            -moz-transform: scaleX(1);
                            -ms-transform: scaleX(1);
                            -o-transform: scaleX(1);
                            transform: scaleX(1);
                        }
                    }
                }

            }
            &.active {
                a {
                    background: transparent;
                    background: none;
                    @media screen and (max-width: $screen-xs) {
                        color: $brand-primary;
                    }
                    span {
                        &:before {
                            visibility: visible;
                            -webkit-transform: scaleX(1);
                            transform: scaleX(1);
                            background-color: $brand-primary;
                            @media screen and (max-width: $screen-xs) {
                                background-color: transparent;

                            }
                        }
                    }
                }
                @media screen and (max-width :$screen-md) {
                    a {
                        background: transparent;
                        background: none;
                        @media screen and (max-width: $screen-xs) {
                            color: $brand-primary;
                        }
                        span {
                            display: inline-block;
                            color: $brand-primary;
                            &:before {
                                bottom: 0;
                                height: 0;
                                background: transparent;
                            }
                        }
                    }
                }
            }
            &.call-to-action{
                margin-left: 5px;
                a {
                    // border: 2px solid $brand-primary;
                    // background: $brand-primary;
                    padding: 5px 10px 3px 10px;
                    margin: 12px 0 0 0px;
                    color: $brand-white;
                    @include border-radius(30px);
                    &:hover {
                        background-color: $brand-primary;
                        color: $brand-white;
                    }
                    span {
                        &:before {
                            background-color: transparent;
                        }

                    }
                    &.sign-up{
                        border: 2px solid $brand-secondary;
                        background: $brand-secondary;
                        &:hover{
                            background: $brand-secondary;
                            color: $brand-white;
                        }

                    }
                    &.log-in{
                        border: 2px solid $brand-primary;
                        background: $brand-primary;
                        &:hover{
                            background: $brand-primary;
                            color: $brand-white;
                        }
                    }
                }
            }
        }
    }
    .navbar-brand {
        float: left;
        display: block;
        font-size: 24px;
        font-weight: 700;
        padding-left: 28px;
        color: #535659;
        text-transform: uppercase;
        letter-spacing: 3px;
        position: relative;
        &:before{
            left: 0px;
            background: rgba($brand-primary,.8);
        }
        > span {
            color: $brand-primary;
        }
        @media screen and (max-width: $screen-md) {
            padding-left: 55px!important;
            padding-right: 0!important;
            &:after, &:before {
                margin-left: 25px;
            }
        }

    }
    &.navbar-fixed-top {
        position: fixed!important;
        background: $brand-white;
        -webkit-box-shadow: 0 0 9px 0 rgba(0,0,0,.1);
        -moz-box-shadow: 0 0 9px 0 rgba(0,0,0,.1);
        -ms-box-shadow: 0 0 9px 0 rgba(0,0,0,.1);
        box-shadow: 0 0 9px 0 rgba(0,0,0,.1);
        margin-top: 0px;
        top: 0;
        .navbar-brand {
            color: #535659;
        }
        #navbar {

            li {
                a {
                    @include transition(.3s);
                    &:hover {
                        color: $brand-primary;
                    }
                    @media screen and (max-width :$screen-md) {
                        font-size: 20px;
                    }
                }
                &.active {
                    a {
                        color: $brand-primary!important;
                        @media screen and (max-width: $screen-sm) {
                            color: $brand-primary!important;
                        }

                        span {
                            &:before {
                                visibility: visible;
                                -webkit-transform: scaleX(1);
                                transform: scaleX(1);
                                background-color: $brand-primary;
                                @media screen and (max-width: $screen-sm) {
                                    background-color: transparent;
                                }

                            }
                        }
                    }
                }
                &.call-to-action{
                    a {
                        border: 2px solid $brand-primary;
                        padding: 5px 10px 3px 10px;
                        margin: 12px 0 0 0px;
                        color: $brand-white;
                        @include border-radius(30px);
                        &:hover {
                            background-color: $brand-primary;
                            color: $brand-white!important;
                        }
                        span {
                            &:before {
                                background-color: transparent;
                            }

                        }
                        &.sign-up{
                            border: 2px solid $brand-secondary;
                            background: $brand-secondary;
                            &:hover{
                                background: $brand-secondary;
                                color: $brand-white;
                            }

                        }
                        &.log-in{
                            border: 2px solid $brand-primary;
                            background: $brand-primary;
                            &:hover{
                                background: $brand-primary;
                                color: $brand-white;
                            }
                        }
                    }
                }
            }
        }
    }
    .navbar-default {
        border: transparent;
        background: $brand-white;
        margin: 0;
        padding-left: 50px;
        padding-right: 50px;
        @include border-radius(0px);
        @media screen and (max-width: $screen-sm) {
            margin-top: 0px;
            padding-right: 0px;
            padding-left: 0px;
        }
    }

}

#fh5co-home {
    background-color: transparent;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    width: 100%;

    background-color: lighten($brand-black, 90%);
    color: #fff;
    // overflow: hidden;
    a {
        color: rgba(255,255,255,.8)!important;
        text-decoration: underline;
        &:hover {
            color: $brand-white;
            text-decoration: none;
        }
    }
    .gradient {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 2;
        // opacity: .4;
        background: rgba(0, 0, 0, .3);
        // -webkit-backface-visibility: hidden; // for Chrome Windows

        // @include fh5co-gradient;
    }
    &, .text-wrap {
        height: 900px;
    }
    .text-wrap {
        display: table;
        width: 100%;
        position: relative;
        z-index: 4;
    }
    .text-inner {
        display: table-cell;
        vertical-align: middle;
        // text-align: center;

        h1 {
            font-size: 48px;
            text-transform: uppercase;
            color: rgba(255,255,255,1);
            margin: 0 0 20px 0;
            font-weight: 400;
            line-height: 58px;
            span{
                color: $brand-primary;
            }
        }
        h2 {
            font-size: 20px;
            line-height: 30px;
            font-weight: 300;
            color: rgba(255,255,255,.8);
            margin: 0 0 60px 0;
        }
        .call-to-action{
            display: block;
            width: 100%;

            a{
                width: 230px;
                display: inline-block;
                font-size: 20px;
                padding: 15px 0;
                margin-right: 10px;

                -webkit-box-shadow: 0px 3px 6px -1px rgba(0,0,0,0.19);
                -moz-box-shadow: 0px 3px 6px -1px rgba(0,0,0,0.19);
                -ms-box-shadow: 0px 3px 6px -1px rgba(0,0,0,0.19);
                -o-box-shadow: 0px 3px 6px -1px rgba(0,0,0,0.19);
                box-shadow: 0px 3px 6px -1px rgba(0,0,0,0.19);

                @include border-radius(4px);

                i{
                    margin-right: 5px;
                    margin-top: 5px;
                    font-size: 18px;
                }

                &.demo{
                    border: 2px solid $brand-primary;
                    background: $brand-primary;
                    text-decoration: none!important;
                    @media screen and (max-width :$screen-sm) {
                        margin-bottom: 20px;
                    }
                }

                &.download{
                    border: 2px solid $brand-secondary;
                    background: $brand-secondary;
                    text-decoration: none!important;
                }
            }
        }
    }
}


